.App {
  font-family: sans-serif;
  max-width: 600px;
  min-height: 60vh;
  margin: auto;
}
.logo {
  display: flex;
}

.logo-image {
  max-width: 100px;
  position: relative;
  top: -10px;
}

.genre-list {
  padding-left: 0%;
  margin-bottom: 2rem;
}

.genre-list-item {
  display: inline;
  margin-right: 1rem;
  background-color: #27272a;
  padding: 0.5rem 1.5rem;
  font-size: large;
  border-radius: 1rem;
  color: white;
  cursor: pointer;
}
.songs-list {
  background-color: #27272a;
  margin: 1rem 0rem;
  max-width: fit-content;
  padding: 1rem;
  border-radius: 1rem;
  color: white;
}
